 integralidad de la atención en salud. En este sentido, la Ruta Integral de Atención para la Promoción y Mantenimiento de la Salud y la Ruta Integral de Atención para la Población Materno – Perinatal, definen e integran las intervenciones individuales, colectivas, poblacionales y las acciones de gestión de la salud pública, requeridas para la promoción de la salud y la gestión oportuna e integral de los principales riesgos en salud de los individuos, las familias y las comunidades (cuyos lineamientos técnicos y operativos fueron adoptados mediante la Resolución 3280 de 2018). Una de las innovaciones incorporadas en las rutas, son las finalidades de valoración integral en salud y educación para la salud, que se suman a las previamente establecidas (detección temprana y protección específica). En ese sentido, se hace indispensable para la implementación adecuada de las rutas, el desarrollo de capacidades en el talento humano en salud en los procedimientos definidos en la RPMS.")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container tarjeta tarjeta__template--azul-claro p-4 p-md-5 mb-5",attrs:{"data-aos":"fade-up"}},[_c('div',{staticClass:"titulo__template--a mb-4"},[_c('span',{staticClass:"h4"},[_vm._v("3. Población"),_c('br'),_vm._v("objetivo")])]),_c('p',{staticClass:"mb-0"},[_vm._v("La población objeto corresponde al talento humano en salud de nivel profesional, específicamente profesionales de Medicina, Enfermería, Medicina Familiar y Pediatría; de las direcciones territoriales y municipales de salud, IPS, EPS y otras instituciones que estén interesados, con una cobertura a nivel nacional, de acuerdo a los cupos disponibles y acordados con el SENA.")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container tarjeta tarjeta__template--azul-claro p-4 p-md-5 mb-5",attrs:{"data-aos":"fade-up"}},[_c('div',{staticClass:"titulo__template--a mb